// byte(2byte, 1byte)
function renderText( str, length ) {
  let string = ""
  let count = 0

  for (let i = 0; i < str.length; i++) {
    if (count < length) {
      if (/[^\x00-\xff]/g.test(str[i])) { // 2byte
        if(count === length - 1) { // length
          count = count + 2
          return string + '...'
        }else {
        count = count + 2
        string = string + str[i]
        }
      } else { // 1byte
        count++
        string = string + str[i]
      }
    } else {
      return string + '...'
    }
  }
  return string 
}

export default renderText